.inline {
  display: flex;
  flex-wrap: wrap;
  margin-left: 26px;
}

.MenuDrinks {
  margin-left: 60px;
  margin-bottom: 25px;
}

.card {
  font-size: 12px;
  border-radius: 15px;
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.2);
  padding: 26px 35px;
  width: 209px;
  height: 460px;
  margin: 13px;
  margin-right: 10px;
  transition: 0.3s ease-in-out;
}

.card:hover {
  color: black;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
}

.DrinkName {
  transform: translateX(-21px);
  font-size: 11px;
  width: 260px;
  margin: 16px 0px;
}

.desc {
  color: rgb(76, 76, 76);
}

.price {
  margin-top: 20px;
}

h2 {
  transform: translateY(13px);
}

.btn {
  margin-left: 17px;
  margin-top: 17px;
  border: 2px solid #d3d3d3;
  border-radius: 15px;
  box-sizing: border-box;
  color: #3B3B3B;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  padding: 16px 24px;
  transition: all 300ms cubic-bezier(0.23, 1, 0.32, 1);
  width: 191px;
}

.btn:disabled {
  pointer-events: none;
}

.btn:hover {
  color: #fff;
  background-color: #1A1A1A;
  box-shadow: rgba(0, 0, 0, 0.25) 0 8px 15px;
  transform: translateY(-2px);
}

.btn:active {
  box-shadow: none;
}

.imgDrink {
  width: 204px;
  transform: translateX(-10px);
  transition: 0.3s ease-in-out;
}

