@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,600;1,300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lexend+Mega:wght@600&display=swap");
.Navbar {
  border: 2px solid rgb(236, 236, 236);
  border-radius: 15px 15px 0 0;
  box-shadow: 0px 2px 20px 0px rgba(0, 0, 0, 0.2);
  transition: 0.1s;
  height: 75px;
}

.Navbar:hover {
  border-color: rgb(225, 225, 225);
}

.headerContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  transform: translateY(15px);
}

.backgroundAdiPizza {
  height: 45px;
  margin-left: 10px;
  border-radius: 12px;
  padding: 1px;
  transition: 0.3s ease-in-out;
}

.backgroundAdiPizza:hover {
  background-color: rgb(220, 220, 220);
  box-shadow: inset 0px 0px 5px rgba(0, 0, 0, 0.1);
}

.logoName {
  letter-spacing: -2px;
  font-family: "Lexend Mega", sans-serif;
  color: rgb(0, 0, 0);
  font-size: 30px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  margin-right: 10px;
  margin-top: -40px;
  margin-left: 60px;
  transform: translateY(1px);
}

.icon {
  width: 60px;
  transform: translateY(5px);
}

.description {
  font-size: 9px;
  transform: translateX(-75px);
}
.description .time {
  transform: translateX(20px);
  font-size: 12px;
}

.phoneNumber {
  font-size: 7px;
  width: 13rem;
  transform: translateX(90px);
  cursor: pointer;
}
.phoneNumber .phoneNumberText {
  transform: translateX(20px);
  font-size: 10px;
}

header h1 {
  margin-top: 5px;
  margin-left: 20px;
}

header ul li {
  display: inline-block;
  margin: 0px 3px;
  cursor: pointer;
  color: rgb(68, 68, 68);
  transition: 0.1s;
}

.join {
  transform: translateX(-10px);
  border-radius: 20px;
  background-color: rgb(191, 191, 191);
  color: rgb(81, 81, 81);
  padding: 9px 20px;
  transition: 0.5s;
}

.join:hover {
  color: black;
  background-color: rgb(219, 219, 219);
}

.korzinaWithNubmer {
  background-color: rgb(255, 115, 0);
  border-radius: 30px;
  width: 100px;
  padding: 11px;
  margin-right: 25px;
  color: white;
  transition: 0.4s ease-in-out;
}

.korzinaWithNubmer:hover {
  background-color: rgb(35, 35, 35);
  color: rgb(255, 255, 255);
}

.korzina {
  background-color: rgb(255, 115, 0);
  border-radius: 30px;
  width: 77px;
  padding: 11px;
  margin-right: 25px;
  color: white;
  transition: 0.4s ease-in-out;
}

.korzina:hover {
  background-color: rgb(35, 35, 35);
  color: rgb(255, 255, 255);
}/*# sourceMappingURL=Header.module.css.map */